@import "../GlobalStyles/GlobalStyles.scss";
.blog {
    display: flex;
    margin: auto;
    width: 80vw;
    gap: 20px;
    @media screen and (max-width: 768px) {
        flex-direction: column;
        width: 95%;
        gap: 0px;
        margin-top: 20px;
    }
}
.blog_rank {
    width: 30vw;
    @media screen and (max-width: 768px) {
        width: 100%;
        justify-content: space-between;
        margin: auto;
    }
    h4 {
        color: $blue;
        width: max-content;
        font-size: 1.8rem;
        i {
            font-size: 1.8rem;
            display: inline-block;
            margin-right: 5px;
            transform: translateY(-2px);
        }
        @media screen and (max-width: 768px) {
            font-weight: 500;
            color: $blue;
            border-radius: 8px;
            margin-left: 5px;
            position: relative;
            padding-left: 10px;
            font-size: 1.8rem;
            margin-left: 0px;
            &::after {
                content: "";
                position: absolute;
                width: 4px;
                height: 55%;
                left: -1px;
                bottom: 50%;
                transform: translateY(50%);
                background-color: $blue;
            }
        }
    }
    .rank_btn {
        margin-top: 25px;
        display: flex;
        gap: 5px;
        .active {
            background-color: $blue;
            color: white;
        }
        p {
            font-size: 1.3rem;
            cursor: pointer;
            padding: 1px 15px;
            padding-top: 2px;
            background-color: white;
            color: $blue;
            border: 1px solid $blue;
            border-radius: 5px;
            position: relative;
            &:hover::after {
                width: 100%;
            }
            &::after {
                content: "";
                position: absolute;
                width: 0%;
                height: 100%;
                top: 0;
                left: 0;
                background-color: $fadeClr500;
                transition: 0.15s ease;
            }
        }
    }
    .rank_car {
        padding: 20px 0;
        border-bottom: 1px dotted $fadeClr500;
        display: flex;
        gap: 10px;
        @media screen and (max-width: 768px) {
            justify-content: space-around;
            padding: 20px 15px;
            gap: 20px;
        }
        > span:nth-child(1) {
            > img {
                width: 125px;
                @media screen and (max-width: 768px) {
                    width: 120px;
                }
            }
        }
        .rank_right {
            color: $blue;
            margin: auto;
        }
        .rank_info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 15px 0;
            align-items: flex-start;
            min-width: 150px;

            h5 {
                color: $blue;
                font-size: 1.3rem;
            }
            span {
                display: flex;
                gap: 0;

                p {
                    font-size: 1.2rem;
                    width: max-content;
                    &:nth-child(2) {
                        color: $blue;
                    }
                }
                .rank_name {
                    min-width: 110px;
                    color: $textdes;
                    display: flex;
                    align-items: flex-end;
                    > i {
                        font-size: 1rem;
                        padding-right: 4px;
                        padding-bottom: 2px;
                    }
                }
            }
        }
    }
}
.more_btn {
    color: $blue;
    font-size: 1.3rem;
    font-weight: 600;
    line-height: 20px;
    @media screen and (max-width: 768px) {
        padding-bottom: 10px;
    }
    div,
    span {
        transition: 0.35s ease;
    }
    &:hover {
        div {
            transform: translateY(8px);
        }
        span {
            transform: translateX(8px);
        }
    }
    > span {
        display: inline-block;
        margin-top: 5px;
        margin-left: 5px;
    }
}
.wrapper {
    width: 70vw;
    margin: 0 auto;
    height: auto;
    display: flex;
    flex-direction: column;
    gap: 20px;
    @media screen and (max-width: 768px) {
        width: 100%;
        order: 2;
    }

    .blog_item {
        display: flex;
        gap: 10px;
        padding-bottom: 17px;
        border-bottom: 1px dotted $fadeClr500;
        & > img {
            width: 300px;
            height: 200px;
            border-radius: 8px;
            @media screen and (max-width: 768px) {
                width: 121px;
                height: 81px;
            }
        }
        .blog_info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 5px 0;
            // margin-left: 5px;
            > div {
                display: flex;
                justify-content: space-between;
                width: 95%;
                color: $textClr;
                @media screen and (max-width: 768px) {
                    // width: 85%;
                }
                .blog_author {
                    font-size: 1.1rem;
                }
                .blog_brand {
                    font-size: 1.1rem;

                    text-align: left;
                    position: relative;
                    padding-left: 8px;
                    &::after {
                        content: "";
                        position: absolute;
                        width: 5px;
                        height: 5px;
                        background-color: $mainClr;
                        border-radius: 100rem;
                        top: 50%;
                        left: -2px;
                        transform: translateY(-50%);
                    }
                }
            }
        }
        .blog_title {
            margin-left: -10px;
            @media screen and (max-width: 768px) {
            }
        }
        .blog_title,
        .blog_descripton {
            font-size: 1.8rem;
            text-align: left;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            color: $textClr;
            max-width: 750px;
            padding-right: 20px;
            cursor: pointer;
            transition: 0.3s ease;
            &::after {
                display: none !important;
            }
            @media screen and (max-width: 768px) {
                font-size: 1.3rem;
            }
            &:hover {
                color: $blue;
            }
        }
        .blog_descripton {
            font-size: 1.4rem;
            -webkit-line-clamp: 3;
            margin-top: -30px;
            color: $textdes;
            &:hover {
                color: $textdes;
            }
        }
    }
}
