@import "../../GlobalStyles/GlobalStyles.scss";

.active > .more_btn {
    display: none !important;
}
.more_btn {
    display: block;
    pointer-events: none;
}

.less_btn {
    display: none;
    pointer-events: none;
    margin-top: -15px;
    @media screen and (max-width: 768px) {
    }
}
.active > .less_btn {
    display: block !important;
}

.load_more {
    // display: none;
    display: block;
    position: relative;
    cursor: pointer;
    font-weight: 600;
    width: 85vw;
    height: 50px;
    text-align: center;
    font-size: 1.2rem;
    color: $blue;
    margin-top: -8rem;
    @media screen and (min-width: 769px) {
    }
}
.brand_wrap {
    max-width: 68vw;
    padding: 60px 0px;
    padding-bottom: 80px;
    height: auto;
    margin-top: 10px;
    position: relative;
    border-radius: 5px;
    box-shadow: 0 0 4px $fadeClr;
    P > i {
        width: 100%;
        display: inline-block;
        text-align: center;
    }
    @media screen and (max-width: 768px) {
        margin-top: 50px;
        max-width: 95vw;
        box-shadow: 0 0 4px $fadeClr;
    }
    .more {
        position: absolute;
        bottom: 10px;
        left: 50%;
        width: 100px;
        background-color: $fadeClr300;
        transform: translateX(-50%);
        @media screen and (max-width: 768px) {
            width: 78px;
        }
    }

    & > div {
        position: absolute;
        top: -40px;
        left: 50%;
        width: 90px;
        height: 90px;
        transform: translateX(-50%);
        box-shadow: 0 0 3px $fadeClr300;
        border-radius: 100rem;
        background-color: white;
        @media screen and (max-width: 768px) {
            width: 70px;
            height: 70px;
            top: -35px;
        }
        overflow: hidden;
        @include flex(center);

        & > img {
            width: 80px;
            height: 80px;
            @media screen and (max-width: 768px) {
                width: 65px;
                height: 65px;
            }
        }
    }
    & {
        h2 {
            font-size: 1.8rem;
            text-align: center;
            padding-bottom: 1.8rem;
            padding-top: 0.5rem;
            color: $textClr;
            @media screen and (max-width: 768px) {
                font-size: 1.6rem;
                margin-top: -15px;
            }
        }
        .brand_ex {
            color: $blue;
            font-size: 1.55rem;
            @media screen and (max-width: 768px) {
                font-size: 1.4rem;
            }
            cursor: pointer;
        }
        p {
            padding: 0 5rem;
            text-align: justify;
            font-size: 1.6rem;
            color: $textClr;
            line-height: 1.7;
            max-height: 110px;
            transition: 0.4s ease-in-out;
            overflow: hidden;
            @media screen and (max-width: 768px) {
                font-size: 1.4rem;
                padding: 0 2rem;
                line-height: 1.65;
                max-height: 120px;
                overflow: hidden;
            }
            a {
                font-weight: 600;
                color: $blue;
                padding-bottom: 1px;
                border-bottom: 1px dotted $blue;
            }
        }
        .view_more {
            max-height: 1000px;
        }
    }
}
