@import "../GlobalStyles/GlobalStyles.scss";
.wrapper {
    width: 100vw;
    height: 450px;
    padding-top: 30px;
    padding-left: 70px;
    position: relative;
    overflow-x: hidden;
    z-index: 9;
    @include flex(center);
    background-color: $darkClr;
    text-align: left;
    @media screen and (max-width: 768px) {
        flex-direction: column;
        height: auto;
        padding-bottom: 40px;
        padding-left: unset;
    }
    .inner {
        width: 80%;
        height: 90%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        @media screen and (max-width: 768px) {
            width: 94%;
            height: 100%;
            overflow: hidden !important;
            padding-bottom: 40px;
        }
        .info {
            @include flex(center);
            justify-content: space-between;
            gap: 30px;
            flex: 1;
            p {
                font-size: 1.4rem;
                @media screen and (max-width: 768px) {
                    font-size: 1.25rem;
                }
            }
            @media screen and (max-width: 768px) {
                flex-direction: column;
                align-items: flex-start;
                overflow: hidden;
                padding-bottom: 40px;
                gap: 0;
                .mshow {
                    & ~ div {
                        max-height: 0;
                        overflow: hidden;
                    }
                    I {
                        transform: rotate(0deg);
                    }
                }
            }
            & > div:nth-child(1) {
                margin-top: 0;
                > span {
                    display: flex;
                    align-items: center;
                    gap: 20px;
                    height: 50px;
                    padding-bottom: 10px;
                    img {
                        width: 50px;
                        transform: scale(0.9);
                        @media screen and (max-width: 768px) {
                            transform: scale(0.8);
                        }
                    }
                    h5 {
                        font-size: 1.8rem;
                        text-transform: unset;
                        line-height: 40px;
                        @media screen and (max-width: 768px) {
                            font-size: 1.65rem;
                        }
                    }
                }

                padding-right: 40px;
                @media screen and (max-width: 768px) {
                    padding-right: 0px;
                }
                width: 40%;
                display: flex;
                flex-direction: column;
                row-gap: 1px;
                p {
                    text-align: justify;
                    &:hover {
                        color: unset;
                    }
                }
            }
            & > div {
                margin-top: 40px;

                h5 {
                    text-transform: uppercase;
                    color: white;
                    font-size: 1.5rem;
                    height: 35px;
                    display: flex;
                    justify-content: space-between;
                    @media screen and (max-width: 768px) {
                        font-size: 1.35rem;
                    }
                    & span,
                    i {
                        pointer-events: none;
                    }
                    & > i {
                        font-size: 1.8rem;
                        margin-top: -5px;
                        margin-right: 5px;
                        transition: 0.3s ease-in-out;
                        transform: rotate(180deg);
                    }
                    @media screen and (max-width: 768px) {
                        padding: 10px 0;
                        height: auto;
                    }
                }
                width: 25%;
                & > div {
                    color: $graylightClr;
                    font-weight: 300;
                    line-height: 1.4;
                    font-size: 1.4rem;
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    row-gap: 2px;
                    max-height: 1000px;
                    overflow: hidden;
                    transition: 0.4s ease-in-out;
                    @media screen and (max-width: 768px) {
                        width: 100% !important;
                        line-height: 1.6;
                        font-size: 1.25rem;
                    }
                }
                height: 100%;
                color: $graylightClr;
                font-weight: 300;
                line-height: 1.4;
                font-size: 1.25rem;
                position: relative;
                display: flex;
                flex-direction: column;
                row-gap: 2px;
                @media screen and (max-width: 768px) {
                    width: 100% !important;
                    line-height: 1.6;
                    margin-top: 20px;
                }
                p {
                    padding: 5px 0;
                    transition: 0.35s ease-in-out;
                    line-height: 1.65;
                    &:hover {
                        color: $mainClr;
                    }
                }
                a > img {
                    padding: 0;
                    margin-left: -3px;
                    width: 120px;
                    @media screen and (max-width: 768px) {
                        position: relative;
                    }
                }
                @media screen and (max-width: 768px) {
                    width: 100% !important;
                }
            }
        }
        .copy {
            width: 89%;
            height: 30px;
            color: $graylightClr;
            box-shadow: inset 0px 10px 2px -10px $fadeClr;
            padding-top: 22px;
            font-size: 1.25rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            @media screen and (max-width: 768px) {
                flex-direction: column;
                align-items: flex-start;
                height: auto;
                width: 100%;
                row-gap: 20px;
            }

            & a {
                font-size: 3rem;
                cursor: pointer;
                color: #0c88ef;
                > img {
                    width: 120px;
                    padding-right: 40px;
                    transform: translateY(2px);
                }
            }
            .trongphuc {
                font-size: 1.2rem;
                color: $graylightClr;
                transition: 0.35s ease-in-out;
                &:hover {
                    color: $mainClr;
                }
            }
        }
    }
}
