@import "./components/GlobalStyles/GlobalStyles.scss";
.body {
    display: flex;
}
.hide {
    display: none;
}
.top {
    position: fixed;
    bottom: 65px;
    right: 18px;
    font-size: 3.2rem;
    z-index: 99999 !important;
    color: $blue;
    cursor: pointer;
    z-index: 1;
    opacity: 0.9;
    &::after {
        content: " ";
        position: absolute;
        width: 80%;
        height: 30%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -60%);
        background-color: rgb(255, 255, 255);
        z-index: -1;
    }
}
