@import "../../components//GlobalStyles/GlobalStyles.scss";

.wrap {
    width: 100vw;
    min-height: 100vh;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    position: relative;
    margin-left: 10px;
    @media screen and (max-width: 768px) {
        display: unset;
        margin-left: 0px;
    }
}
.side_blog {
    width: 70vw;
    height: auto;
    @media screen and (max-width: 768px) {
        width: 100vw;
        order: 4;
        h3 {
            margin-left: 2.5vw;
        }
    }
}
.all {
    width: 100vw;
    @include flex(center);
    flex-direction: column;
    margin-top: 60px;
    padding-bottom: 50px;
    @media screen and (max-width: 768px) {
        margin-left: 0px;
        margin-top: 25px;
        padding: 0;
        overflow-x: hidden;
        padding-bottom: 0px;
    }
}
.price {
    font-size: 1.1rem;
    color: $blue;
    padding-top: 20px;
    @media screen and (max-width: 768px) {
        width: 85vw;
        text-align: center;
        order: 4;
        padding: 20px 0;
        font-size: 1rem;
    }
}
@media screen and (max-width: 768px) {
    .oder_3 {
        order: 3 !important;
    }
}
.bread_crumb {
    width: 70vw;
    margin-top: 20px;
    @media screen and (max-width: 768px) {
        width: 100%;
        margin-top: 0px;
    }
}

.cars_title {
    width: 65vw;
    text-align: left;
    padding-top: 20px;
    padding-bottom: 10px;
    display: flex !important;
    @media screen and (max-width: 768px) {
        padding-top: 10px;
        width: 100%;
        padding-bottom: 0px;
    }
    span,
    i {
        font-size: 1.5rem;
        @media screen and (max-width: 768px) {
            font-size: 1.2rem;
        }
    }
    p {
        width: max-content;
        white-space: nowrap;
        font-size: 1.3rem;
        color: $blue;
        font-weight: 600;
        cursor: pointer;
        @media screen and (max-width: 768px) {
            font-size: 1.1rem !important;
            padding-right: 6vw;
        }
        & > i {
            transition: 0.25s ease-in-out;
            display: inline-block;
            transform: translateX(2px);
        }
        &:hover {
            & > i {
                transform: translateX(8px);
            }
        }
    }
    h3 {
        font-size: 1.8rem;
        @media screen and (max-width: 768px) {
            font-size: 1.25rem;
        }
    }
    div {
        font-size: 1.8rem;
        font-weight: bold;
        color: $textClr;
        background-color: $fadeClr500;
        width: fit-content;
        padding: 2px 10px;
        border-radius: 3px;
        @media screen and (max-width: 768px) {
            margin-left: 7px;
            font-size: 1.2rem;
        }
    }
}
.wrapper {
    width: 85vw;
    position: relative;
    @include flex(center);
    // padding-bottom: 500px;
    flex-direction: column;
    max-width: 1400px;
    @media screen and (max-width: 768px) {
        flex-direction: column;
        height: 100%;
        padding-top: 30px;
        width: 100vw;
        width: 95vw;
        padding-bottom: 0px;
    }

    .content {
        @include flex(center);
        justify-content: flex-start;
        flex-direction: column;
        // margin-top: -30px;
        width: 80vw;
        // height: auto;
        padding-left: 40px;
        position: relative;
        gap: 30px;

        @media screen and (max-width: 768px) {
            padding-left: 0px;
            width: 100%;
            // height: auto;
            padding-bottom: 10px;
            // padding-top: 60px;
            order: 0 !important;
            margin-top: unset;
            // padding-top: 20px;
        }
    }
    .cars_ofbrand {
        margin-top: 10px;
        @media screen and (max-width: 768px) {
            order: 3;
            margin: 0;
            padding: 0;
        }
    }
    @media screen and (max-width: 768px) {
        right: 0px;
        margin-left: 0px;
    }
}
@media screen and (max-width: 768px) {
    .slider_mobile {
        order: 7 !important;
        display: flex;
        width: 100vw;
        flex-direction: column;
    }
}
.des {
    position: relative;
    width: 80% !important;
}
