@import "../GlobalStyles/GlobalStyles.scss";
.bread_crumb {
    list-style: none;
    width: max-content;
    color: $blue;
    font-size: 1.5rem;
    font-weight: 600;
    padding: 5px 65px 5px 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 5px;
    @media screen and (max-width: 768px) {
        padding: 10px;
        padding-left: 15px;

        font-size: 1.2rem;
    }
    > li {
        padding: 1px 5px;
        cursor: pointer;
    }
    li:nth-last-child(2) {
        color: $textClr;
        opacity: 0.8;
        pointer-events: none !important;
    }
    li:last-child {
        display: none;
    }
    .bread_crumb-link {
        padding: 10px 0;
        > i {
            font-size: 1.3rem;
        }
    }
}
