@import "../../GlobalStyles/GlobalStyles.scss";

.list_wrap {
    @media screen and (max-width: 768px) {
        ::-webkit-scrollbar {
            width: 4px !important;
            background: transparent; /* make scrollbar transparent */
        }
    }
    display: flex;
    position: sticky;
    right: 10px;
    top: 90px;
    @media screen and (max-width: 768px) {
        // position: fixed;
        position: unset;
        right: unset;
        top: unset;
        transform: unset;
        margin-right: 0px;
        order: 7 !important ;
        // height: auto;
        margin-top: 40px;
        // transform: translateY(40px);
    }
    .list_alpha > ul {
        height: 82vh;
        background-color: $blue;
        color: white;
        width: 30px;
        font-size: 1.3rem;
        list-style: none;
        overflow: hidden;
        border-radius: 5px;
        gap: 20px;
        @include flex(center);
        flex-direction: column;
        & > li > a {
            text-align: center;
            padding: 15px 15px;
            transition: 0.2s ease;
            font-weight: 500;
            @media screen and (max-width: 768px) {
                padding: 10px 150px;
            }
            cursor: pointer;
            &:hover {
                background-color: white;
                color: $blue;
            }
        }
    }

    .list > ul {
        direction: rtl;
        width: 240px;
        height: 82vh;
        overflow-y: scroll;
        overflow-x: hidden;
        display: flex;
        align-items: flex-end;
        flex-direction: column;
        justify-content: flex-start;
        list-style: none;
        scroll-behavior: smooth;
        @media screen and (max-width: 768px) {
            margin-top: -20px;
            width: 100vw;
            height: 50vh;
        }

        & .list_item {
            padding: 0rem 0rem;
            width: 220px;
            height: 60px;
            padding-left: 3rem;
            @include flex(center);
            justify-content: flex-end;
            gap: 30px;
            border-bottom: 1px dotted $fadeClr500;
            transition: 0.2s ease;
            cursor: pointer;
            @media screen and (max-width: 768px) {
                width: 100vw;
                padding-left: 3rem;
                height: 60px;
            }
            &:hover {
                background-color: $fadeClr500;
            }
            & > img {
                width: 42px;
                height: 42px;
                pointer-events: none;
            }
            & h5 {
                font-size: 1.3rem;
                color: $textClr;
                pointer-events: none;
            }
        }
    }
}
.active {
    background-color: $fadeClr500;
}
