@import "../../GlobalStyles/GlobalStyles.scss";
.wrapper {
    color: $textClr;
    border-bottom: 1px dotted $fadeClr;
    padding-bottom: 1.8rem;
    &:hover .car_name {
        color: $blue;
    }
    &:active .car_name {
        transform: translateX(-3px);
    }
    &:hover .car_avatar {
    }
    &:active .car_avatar {
        // transform: translateX(3px);
    }
    @include flex(center);
    justify-content: flex-start;
    cursor: pointer;
    gap: 30px;
    font-size: 1.2rem;
    width: 42rem;
    @media screen and (max-width: 768px) {
        gap: 23px;
        width: 90vw;
    }
    .car_avatar {
        padding-right: 5px;
        margin-left: 10px;
        transition: 0.2s ease-in;
        & img {
            width: 110px;
        }
        @media screen and (max-width: 768px) {
            width: 120px;
            height: 60px;
            margin-left: 2px;
            padding-right: 0px;
        }
    }

    .car_info {
        @include flex(center);
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        min-width: 15rem;
        height: 55px;
        @media screen and (max-width: 768px) {
            min-width: 12rem;
            .car_name {
                @include flex(center);
                font-size: 1.2rem;
                color: $blue !important;
            }
        }
        .car_name {
            @include flex(center);
            gap: 20px;
            font-size: 1.5rem;
            transition: 0.35s ease;
            @media screen and (max-width: 768px) {
                font-size: 1.25rem;
            }
        }
    }
    .car_price {
        font-weight: bold;
        @include flex(center);
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        height: 53px;
        min-width: 10rem;
        transform: translateX(-7px);

        @media screen and (max-width: 768px) {
            font-size: 1.1rem;
            transform: translateX(-3px);
            height: 53px;
            margin-top: 2px;

            .car_price-vnd {
                font-size: 1.1rem;
            }
        }
        .car_price-vnd {
            font-weight: 500;
            font-size: 1.1rem;
        }
    }
    &:hover .car_version {
        color: $blue;
    }
    .car_description {
        & span:nth-child(1) {
            &::after {
                position: absolute;
                content: "";
                background-color: $mainClr;
                width: 5px;
                height: 5px;
                left: -5px;
                border-radius: 100rem;
                top: 50%;
                transform: translateY(-50%);
            }
        }
        & span {
            font-size: 1.2rem;
            // font-weight: 600;
            // background-color: $fadeClr500;
            padding: 1px 4px;
            transform: translateX(-2px);
            border-radius: 3px;
            @media screen and (max-width: 768px) {
                font-size: 1.1rem;
                padding: 1px 3px;
            }
        }
        display: flex;
        gap: 7px;
    }
}
.no_result {
    width: 40rem;
    font-size: 1.3rem;
}
