@import "../GlobalStyles/GlobalStyles.scss";
.blog {
    display: flex;
    margin: auto;
    width: 95%;
    @media screen and (max-width: 768px) {
        flex-direction: column;
        width: 95%;
        gap: 0px;
        margin-top: 20px;
    }
}
.more_btn {
    color: $blue;
    font-size: 1.3rem;
    font-weight: 600;
    line-height: 20px;
    @media screen and (max-width: 768px) {
        padding-bottom: 10px;
    }
    div,
    span {
        transition: 0.35s ease;
    }
    &:hover {
        div {
            transform: translateY(8px);
        }
        span {
            transform: translateX(8px);
        }
    }
    > span {
        display: inline-block;
        margin-top: 5px;
        margin-left: 5px;
    }
}
.wrapper {
    padding-top: 20px;
    margin: 0 auto;
    height: auto;
    display: flex;
    flex-direction: column;
    gap: 10px;
    text-align: center;
    @media screen and (max-width: 768px) {
        width: 100%;
        padding-top: 0px;
    }

    .blog_item {
        display: flex;
        padding-bottom: 17px;
        border-bottom: 1px dotted $fadeClr500;
        gap: 20px;
        cursor: pointer;
        &:hover {
            .blog_title {
                color: $blue;
            }
        }
        & > img {
            width: 125px;
            border-radius: 5px;
            object-fit: cover;
            @media screen and (max-width: 768px) {
                width: 121px;
            }
        }
        .blog_info {
            display: flex;
            flex-direction: column;
            gap: 5px;
        }
        .blog_info p {
            width: 100%;
            text-align: left;
            font-size: 1.2rem;
            position: relative;
            padding-left: 5px;
            @media screen and (max-width: 768px) {
                font-size: 1.1rem;
            }
            &::after {
                content: "";
                position: absolute;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background-color: $mainClr;
                top: 50%;
                left: -5px;
                transform: translateY(-50%);
            }
        }
        .blog_title {
            font-size: 1.5rem;
            text-align: left;
            overflow: hidden;
            text-overflow: ellipsis;
            font-weight: 500;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            color: $textClr;
            max-width: 750px;
            padding-right: 10px;
            cursor: pointer;
            margin-bottom: 5px;
            transition: 0.3s ease-in-out;
            margin-left: -10px;
            &::after {
                display: none !important;
            }
            @media screen and (max-width: 768px) {
                font-size: 1.8rem;
                padding-right: 0px;
            }
        }
    }
}
