@import "../GlobalStyles/GlobalStyles.scss";
.blog_rank {
    width: 30vw;
    margin-top: -40px;
    @media screen and (max-width: 768px) {
        width: 100%;
        margin-top: 0;
        justify-content: space-between;
        margin: auto;
    }
    h4 {
        color: $blue;
        width: max-content;
        font-size: 1.8rem;
        i {
            font-size: 1.8rem;
            display: inline-block;
            margin-right: 5px;
            transform: translateY(-2px);
        }
        @media screen and (max-width: 768px) {
            font-weight: 500;
            color: $blue;
            border-radius: 8px;
            margin-left: 5px;
            position: relative;
            padding-left: 10px;
            font-size: 1.4rem;
            margin-left: 0px;
            &::after {
                content: "";
                position: absolute;
                width: 4px;
                height: 55%;
                left: -1px;
                bottom: 50%;
                transform: translateY(50%);
                background-color: $blue;
            }
        }
    }
    .rank_btn {
        margin-top: 20px;
        padding-bottom: 15px;
        display: flex;
        gap: 5px;

        .active {
            background-color: $blue;
            color: white;
        }
        p {
            font-size: 1.3rem;
            cursor: pointer;
            padding: 2px 17px;
            transition: 0.25s ease-in-out;
            @media screen and (max-width: 768px) {
                padding: 2px 20px;
            }
            padding-top: 2px;
            background-color: white;
            color: $blue;
            border: 1px solid $blue;
            border-radius: 5px;
            position: relative;
            &:hover::after {
                width: 100%;
            }
            &::after {
                content: "";
                position: absolute;
                width: 0%;
                height: 100%;
                top: 0;
                left: 0;
                background-color: $fadeClr500;
                transition: 0.15s ease;
            }
        }
    }
    .rank_car {
        padding: 20px 0;
        border-bottom: 1px dotted $fadeClr500;
        display: flex;
        gap: 10px;
        @media screen and (max-width: 768px) {
            justify-content: space-around;
            padding: 18px 15px;
            gap: 25px;
        }
        > span:nth-child(1) {
            display: flex;
            justify-content: center;
            align-items: center;
            > img {
                width: 130px;
                @media screen and (max-width: 768px) {
                    width: 120px;
                }
            }
        }
        .rank_right {
            color: $blue;
            margin: auto;
        }
        .rank_info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 15px 0;
            align-items: flex-start;
            min-width: 150px;
            gap: 10px;

            h5 {
                color: $blue;
                font-size: 1.5rem;
                @media screen and (max-width: 768px) {
                    font-size: 1.3rem;
                }
            }
            span {
                display: flex;
                gap: 0;

                p {
                    font-size: 1.3rem;
                    width: max-content;
                    @media screen and (max-width: 768px) {
                        font-size: 1.1rem;
                    }
                    &:nth-child(2) {
                        color: $blue;
                    }
                }
                .rank_name {
                    min-width: 110px;
                    color: $textdes;
                    display: flex;
                    align-items: flex-end;
                    > i {
                        font-size: 1rem;
                        padding-right: 4px;
                        padding-bottom: 2px;
                    }
                }
            }
        }
    }
}
.more_btn {
    color: $blue;
    font-size: 1.3rem;
    font-weight: 600;
    line-height: 20px;
    @media screen and (max-width: 768px) {
        padding-bottom: 30px;
        padding-top: 20px;
    }
    div,
    span {
        transition: 0.35s ease;
    }
    &:hover {
        div {
            transform: translateY(8px);
        }
        span {
            transform: translateX(8px);
        }
    }
    > span {
        display: inline-block;
        margin-top: 5px;
        margin-left: 5px;
    }
}
