@import "../../components//GlobalStyles/GlobalStyles.scss";

.all {
    height: auto;
    scroll-behavior: smooth;
    text-align: center;
    overflow: hidden;
    margin-left: 30px;
    padding-bottom: 70px;
    max-width: 1650px;
    margin: 0 auto;
    margin-top: 100px;
    width: 100vw;
    @media screen and (max-width: 768px) {
        height: 100%;
        margin-left: 0px;
        margin-top: 50px;
        padding-bottom: 10px;
    }
}
.brand_title {
    @include flex(center);
    margin: auto;
    width: 80vw;
    max-width: 1650px;
    // padding: 10px 15rem;
    padding-top: 2rem;
    // margin-top: -23px;
    padding-bottom: 15px;
    justify-content: space-between;
    @media screen and (max-width: 768px) {
        width: 94vw;
        padding: 10px 10px;
        margin-top: 0px;
    }
    h2,
    h1 {
        font-weight: 600;
        font-size: 1.8rem;
        color: $blue;
        border-radius: 8px;
        margin-left: 5px;
        position: relative;
        padding-left: 10px;
        &::after {
            content: "";
            position: absolute;
            width: 4px;
            height: 55%;
            left: -1px;
            bottom: 50%;
            transform: translateY(50%);
            background-color: $blue;
        }
        @media screen and (max-width: 768px) {
            font-size: 1.4rem;
            margin-left: 0px;
        }
    }
    span {
        font-weight: 600;
        font-size: 1.2rem;
        cursor: pointer;
        margin-right: 40px;
        color: $blue;
        padding-bottom: 1px;
        border-bottom: 1px dotted transparent;
        transition: 0.35s ease-in-out;
        @media screen and (max-width: 768px) {
            font-size: 1.15rem;
        }

        & i {
            margin-left: 4px;
            transition: 0.35s ease-in-out;
        }
        &:hover {
            border-bottom: 1px dotted $blue;
            i {
                margin-right: -2px;
            }
        }
        @media screen and (max-width: 768px) {
            margin-right: 7px;
        }
    }
}
.wrapper1 {
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    @media screen and (max-width: 768px) {
        margin-left: 0px;
        margin-top: 15px;
        order: 2 !important;
    }

    .wrapper_swiper {
        max-width: 1650px;
        width: 100vw;
        height: 100%;
        position: relative;
        text-align: center;
        @media screen and (max-width: 768px) {
            width: 100vw;
        }
        .news {
            width: 80vw;
            margin: 0 auto;
            display: flex;
            gap: 15px;
            @media screen and (max-width: 768px) {
                flex-direction: column;
                width: 95%;
                gap: 5px;
            }
            .news_big {
                width: 65%;
                height: 500px;
                border-radius: 8px;
                overflow: hidden;
                position: relative;
                @media screen and (max-width: 768px) {
                    width: 100%;
                    height: 200px;
                }
                @media screen and (min-width: 1650px) {
                    height: 570px;
                }
                h4,
                > div {
                    position: absolute;
                    height: auto;
                    font-size: 2.5rem;
                    text-align: justify;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    bottom: 60px;
                    left: 30px;
                    text-align: left;
                    padding-right: 30px;
                    cursor: pointer;
                    color: white;
                    z-index: 1;
                    transition: 0.35s ease-in-out;
                    overflow: hidden !important;
                    @media screen and (max-width: 768px) {
                        font-size: 1.5rem;
                        bottom: 40px;
                        left: 10px;
                        padding-right: 10px;
                    }
                    &:hover {
                        color: $mainClr;
                    }
                }
                > div {
                    display: flex;
                    width: 90%;
                    justify-content: space-between;
                    font-size: 1.4rem;
                    bottom: 20px;
                    @media screen and (max-width: 768px) {
                        font-size: 1.1rem;
                        bottom: 15px;
                        padding-right: 10px;
                    }
                    &:hover {
                        color: white;
                    }
                    p {
                        transition: 0.35s ease-in-out;

                        &:hover {
                            color: $mainClr;
                        }
                    }
                }
                &::after {
                    content: "";
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    background: linear-gradient(transparent, black);
                    opacity: 0.7;
                }
                img {
                    width: 100%;
                    height: 100%;
                    display: block;
                    object-fit: cover;
                }
            }
            .news_small {
                height: 500px;
                width: 35%;
                display: flex;
                flex-direction: column;
                gap: 15px;
                @media screen and (min-width: 1650px) {
                    height: 570px;
                }
                @media screen and (max-width: 768px) {
                    width: 100%;
                    height: 100px;
                    flex-direction: row;
                    gap: 5px;
                }
                h4 {
                    position: absolute;
                    height: auto;
                    font-size: 1.8rem;
                    bottom: 15px;
                    line-height: 1.6;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    left: 15px;
                    padding-right: 15px;
                    cursor: pointer;
                    text-align: left;
                    transition: 0.35s ease-in-out;
                    color: white;
                    z-index: 1;
                    overflow: hidden !important;
                    @media screen and (max-width: 768px) {
                        font-size: 1.1rem;
                        bottom: 5px;
                        left: 5px;
                        padding-right: 5px;
                        overflow: hidden;
                    }
                    &:hover {
                        color: $mainClr;
                    }
                }
                > div {
                    position: relative;
                    height: 245px;
                    border-radius: 8px;
                    overflow: hidden;
                    @media screen and (min-width: 1650px) {
                        height: 280px;
                    }
                    @media screen and (max-width: 768px) {
                        width: 100%;
                        height: 100px;
                        flex-direction: row;
                    }
                    &::after {
                        content: "";
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        left: 0;
                        background: linear-gradient(transparent, black);
                        opacity: 0.7;
                    }
                }
                img {
                    width: 100%;
                    height: 100%;
                    display: block;
                    object-fit: cover;
                }
            }
        }
    }
}

.wrapper2 {
    @include flex(center);
    flex-direction: column;
    max-width: 1650px;
    @media screen and (max-width: 768px) {
        margin-left: 0px;
        margin-top: 30px;
    }

    .wrapper_swiper {
        width: 85vw;
        border-radius: 8px;
        max-width: 1950px;
        padding: 10px 0;
        background-color: white;
        height: 100%;
        position: relative;
        @media screen and (max-width: 768px) {
            width: 98vw;
            padding: 0;
        }
        .swiper {
            width: 100%;
            height: 260px;
            max-width: 1650px;
            overflow: hidden;
            @media screen and (min-width: 1700px) {
                height: 280px;
            }
            @media screen and (max-width: 768px) {
                width: 95%;
                height: 170px;
                margin-top: -10px;
                margin-bottom: -10px;
            }
            .swiper_item {
                width: 100px;
                height: 105px;
                @media screen and (min-width: 1700px) {
                    width: 100px;
                    height: 125px;
                }
                @media screen and (max-width: 768px) {
                    height: 80px;
                }
                & img {
                    width: 62px;
                    height: 62px;
                    transition: 0.35s ease-in-out;

                    cursor: default !important;
                    @media screen and (max-width: 768px) {
                        width: 40px;
                        height: 40px;
                    }
                }
                & h6 {
                    font-size: 1.3rem;
                    color: $textClr;
                    transition: 0.35s ease-in-out;
                    text-align: center;
                    cursor: pointer;

                    @media screen and (max-width: 768px) {
                        font-size: 1rem;
                        margin-top: 0px;
                        font-weight: 500;
                    }
                }
                &:hover {
                    img {
                        transform: scale(1.03);
                    }
                    h6 {
                        color: $blue;
                    }
                }
            }
        }
    }
}
