.wrapper3 {
    margin: 0 auto;
    margin-bottom: -40px;
    width: 80vw;
    transform: translateX(-10px);
    @media screen and (max-width: 768px) {
        width: 100vw;
    }
    .wrapper_swiper {
        @media screen and (max-width: 768px) {
            margin-left: 0px;
            width: 100vw;
        }
        .swiper {
            width: 100%;
            height: 390px;
            margin: 0 auto;
            overflow: hidden;
            background-color: white;
            @media screen and (max-width: 768px) {
                width: 99%;
                height: 310px;
                margin-bottom: 40px;
            }
            .swiper_item {
                width: 200px;
                height: 190px;
                @include flex(center);
                flex-direction: column;
                row-gap: 10px;
                @media screen and (max-width: 768px) {
                    row-gap: 0px;
                    width: 200px;
                    height: 150px;
                }
                h5 {
                    // margin-top: -10px;
                    padding-bottom: 10px;
                    font-size: 1.5rem;
                    color: $textClr;
                    transition: 0.35s ease-in-out;
                    @media screen and (max-width: 768px) {
                        font-size: 1.3rem;
                    }
                    cursor: pointer;
                    @media screen and (max-width: 768px) {
                        margin-top: 0px;
                        padding-bottom: 0px;
                    }
                }
                & img {
                    width: 140px;
                    height: 74.6666px;
                    transition: 0.35s ease-in-out;

                    cursor: default !important;
                    @media screen and (max-width: 768px) {
                        width: 120px;
                        height: 64px;
                    }
                }
                &:hover {
                    img {
                        transform: scale(1.02);
                    }
                    h4 {
                        color: $blue;
                    }
                    .car_info {
                        // color: $mainClr;
                    }
                }
            }
            .car_info {
                @include flex(center);
                gap: 15px;
                font-size: 1.2rem;
                color: $textClr;
                font-weight: 500;
                transition: 0.35s ease-in-out;

                @media screen and (max-width: 768px) {
                    font-size: 1.1rem;
                    gap: 10px;
                    margin-top: 5px;
                }
                & p {
                    background-color: $fadeClr500;

                    padding: 0px 6px;
                    border-radius: 5px;
                    font-weight: 500;
                    color: $textClr;
                    @media screen and (max-width: 768px) {
                        padding: 0px 5px;
                        background-color: transparent;
                        font-weight: 500;
                    }
                }
            }
        }
    }
}
