@import "../../GlobalStyles/GlobalStyles.scss";

.wrapper {
    @media screen and (max-width: 768px) {
        // padding-bottom: 100px;
    }
    h5 {
        font-size: 1.2rem;
        margin-left: 0px;
        background-color: $fadeClr500;
        width: max-content;
        padding: 2px 5px;
        border-radius: 3px;
        font-weight: 600;
        color: $blue;
    }
    width: 100%;
    margin: 0;
}
.btn_mobile {
    color: $blue;
    padding: 5px;
    padding-right: 0px;
    padding-left: 20px;
    transform: translateX(-20px);
}
.inner {
    padding: 0 15px;
    width: 95vw;
    .car {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom: 20px;
        padding-left: 20px;
        padding-top: 20px;
        border-bottom: 1px dotted $fadeClr500;
        @media screen and (max-width: 768px) {
            padding: 17px 0 17px 20px;
        }
        gap: 30px;
        .car_img {
            display: flex;
            align-items: center;
            img {
                width: 140px;
                object-fit: cover;
                transition: 0.35s ease-in-out;
                @media screen and (max-width: 768px) {
                    width: 120px;
                }
            }
        }
        .car_info {
            flex: 1;
            display: flex;
            height: 80px;
            align-items: flex-start;
            justify-content: center;
            flex-direction: column;
            gap: 15px;

            & > p {
                color: $blue;
                font-weight: 600;
                font-size: 1.8rem;
                cursor: pointer;
                width: 150px;
                &:nth-child(1) {
                    // color: red;
                    transform: translateY(5px);
                }
                @media screen and (max-width: 768px) {
                    font-size: 1.25rem;
                    width: unset;
                }
            }
            & > div {
                display: flex;
                align-items: space-between;
                justify-content: flex-start;
                gap: 15px;

                & > p {
                    font-size: 1.1rem;
                    font-weight: 600;
                    color: $textClr;
                    padding: 1px 5px;
                    border-radius: 3px;
                    margin-left: -4px;

                    @media screen and (max-width: 768px) {
                        font-size: 1.1rem;
                    }
                }
            }
        }
    }
}
@media screen and (min-width: 769px) {
    .wrapper {
        h5 {
            margin-top: 20px;
            color: $textClr;
            background-color: $fadeClr500;
        }
        width: 100vw;
        @include flex(center);
        .car_wrapper {
            box-shadow: 0 0 5px $fadeClr300;
            @include flex(center);
            flex-direction: column;
            transition: 0.35s ease;
            border-radius: 4px;

            &:hover {
                img {
                    transform: scale(1.02);
                }
            }
        }

        .inner {
            max-width: 1150px;
            display: flex;
            justify-content: center;
            margin-left: 40px;
            padding: 0;
            align-items: center;
            flex-wrap: wrap;
            gap: 10px;
        }
        .car {
            &:hover {
                img {
                    // transition-delay: 0.15s;
                    // transform: scale(1.02);
                }
            }
            max-width: 310px;
            border: none;
            padding: 25px 0 25px 15px;
            gap: 15px;

            .car_img {
                max-width: 150px;
                max-height: 80px;
            }
            .car_info {
                gap: 15px;
                p {
                    font-size: 1.45rem;
                }
                > div p {
                    font-size: 1.22rem;
                    // padding-left: 8px;
                    // position: relative;
                    // &::after {
                    //     position: absolute;
                    //     content: "";
                    //     width: 5px;
                    //     height: 5px;
                    //     background-color: $textClr;
                    //     top: 47%;
                    //     transform: translateY(-50%);
                    //     left: -1px;
                    //     border-radius: 100rem;
                    // }
                }
            }
        }
    }
}
