@import "../GlobalStyles/GlobalStyles.scss";
.wrapper {
    margin-top: 10px;
    padding-left: 40px;
    // & > p {
    //     font-size: 1.2rem;
    //     font-weight: bold;
    //     color: $textClr;
    //     background-color: $fadeClr500;
    //     width: fit-content;
    //     padding: 2px 5px;
    //     border-radius: 5px;
    //     @media screen and (max-width: 768px) {
    //         margin-left: 15px;
    //         display: block;
    //     }
    // }
    @media screen and (max-width: 768px) {
        padding-left: 0px;
    }
    .wrapper_swiper {
        .swiper_item {
            font-size: 1.3rem;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            padding-bottom: 25px;
            @media screen and (max-width: 768px) {
                font-size: 1.2rem;
                height: 285px;
                padding-bottom: 5px;
            }

            .swiper_item-link {
                margin: 10px 0;
                border-radius: 5px;
                box-shadow: 0 0 8px $fadeClr300;
                width: 300px;
                display: flex;
                height: 335px;
                padding-bottom: 10px;
                flex-direction: column;
                justify-content: space-between;
                overflow: hidden !important;
                transition: 0.35s ease-in-out;
                @media screen and (max-width: 768px) {
                    width: 220px;
                    height: 250px;
                    padding-bottom: 0px;
                    justify-content: unset;
                    box-shadow: 0 0 4px $fadeClr300;
                }
                & img {
                    transition: 0.35s ease-in-out;
                    cursor: default;
                    height: 170px;
                    object-fit: cover;
                    width: 100%;
                    @media screen and (max-width: 768px) {
                        height: 140px;
                    }
                }
                &:hover {
                    h4 {
                        color: $blue;
                    }
                    @media screen and (min-width: 769px) {
                        transform: translateY(-5px);
                        box-shadow: 0 0 10px $fadeClr300;
                    }
                }
                & div {
                    @include flex(center);
                    flex-direction: column;
                    align-items: flex-start;
                    gap: 2px;
                    row-gap: 10px;
                    margin-left: 5px;
                    color: $textClr;
                    h4,
                    .car_description {
                        color: $textClr;
                        padding: 0 5px;
                        cursor: pointer;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        min-height: 42px;
                        width: 95%;
                        overflow: hidden;
                        margin-top: -5px;
                        transition: 0.25s ease-in-out;
                        font-size: 1.6rem;
                        @media screen and (max-width: 768px) {
                            font-size: 1.2rem;
                            min-height: 36px;
                        }
                    }
                    .car_description {
                        -webkit-line-clamp: 2;
                        opacity: 0.85;
                        font-size: 1.3rem;
                    }
                    .car_info {
                        font-size: 1.2rem;
                        position: relative;
                        margin: 10px 0 0px 15px;
                        @media screen and (max-width: 768px) {
                            font-size: 1rem;
                        }
                        &::after {
                            content: "";
                            position: absolute;
                            top: 50%;
                            left: -10px;
                            transform: translateY(-50%);
                            width: 5px;
                            height: 5px;
                            background-color: $mainClr;
                            border-radius: 100rem;
                        }
                    }
                    .car_author {
                        width: 92%;
                        display: flex;
                        font-size: 1.2rem;
                        flex-direction: row;
                        align-items: center;
                        margin-top: 5px;
                        justify-content: space-between;
                        & > p:nth-child(2) {
                            color: $blue;
                            font-size: 1.1rem;
                            font-weight: 500;
                        }
                        & > p:nth-child(1) {
                            font-size: 1rem;
                        }
                    }
                }
            }
        }
    }
}
