@import "../../components//GlobalStyles/GlobalStyles.scss";
.wrapper {
    .head {
        height: 49px;
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 99998;
        @include flex(center);
        justify-content: space-around;
        font-size: 1.5rem;
        transition: 0.35s ease-in-out;
        // background-color: transparent;
        // color: transparent;
        // // padding: 0 20px;
        transform: translateY(-100%);
        background-color: $blue;
        opacity: 0.95;
        color: white;
        // background-color: white;
        // color: $blue;
        font-weight: 500;
        & span {
            opacity: 0;
        }

        b {
            opacity: 0;
        }

        &.ontop1 {
            transform: translateY(0%);

            & span {
                opacity: 1;
                min-width: 70vw;
                text-align: center;
            }
            & > i {
                font-size: 1.8rem;
            }
        }
    }
    width: 85vw;
    margin: auto;
    margin-top: 70px;
    max-width: 1650px;
    display: flex;
    flex-direction: column;
    @media screen and (max-width: 768px) {
        padding-left: unset;
        width: 100vw;
        overflow: hidden;
        justify-content: center;
        align-items: center;
        margin-left: 0px;
        margin-top: -5px;
    }
    & .top {
        height: 520px;
        margin: 0 auto;
        max-width: 1250px;
        @include flex(center);
        gap: 10px;
        padding-left: 25px;
        box-shadow: 0 0 9px $fadeClr300;
        border-radius: 8px;
        margin-top: 15px;
        overflow: hidden;
        @media screen and (min-width: 1700px) {
            position: relative;
            height: 600px;
            max-width: 1550px;
        }
        @media screen and (max-width: 768px) {
            padding-left: unset;
            height: auto;
            margin-top: unset;
            flex-direction: column;
            box-shadow: 0 0 0px $fadeClr300;
        }

        & .slide_wrapper {
            width: 640px;
            overflow: hidden;
            border-radius: 8px;
            position: relative;
            box-shadow: 0 0 10px $fadeClr300;
            padding-bottom: 5px;
            @media screen and (min-width: 1700px) {
                width: 740px;
            }
            @media screen and (max-width: 768px) {
                width: 100vw;
                margin-bottom: 10px;
                border-radius: 8px;
            }
            .price_wrap {
                padding-top: 10px;
                width: 95%;
                display: flex;
                gap: 10px;
                margin: 0 auto;
                padding-bottom: 5px;

                & > div {
                    width: 290px;
                    height: 40px;
                    padding: 5px 15px;
                    padding-bottom: 20px;
                    border-radius: 8px;
                    box-shadow: 0 0 1px $blue;
                    & span {
                        font-size: 1.35rem;
                        color: $textClr;
                    }
                    & p {
                        text-align: center !important;
                        width: 100%;
                        font-size: 1.8rem !important;
                        font-weight: 600;
                        color: $blue !important;
                    }
                }
            }
            .price {
                display: block;
                width: 100%;
                font-size: 1.1rem;
                color: $blue;
                padding: 5px 0;
                text-align: center !important;
                @media screen and (max-width: 768px) {
                    width: 85vw;
                    text-align: center;
                    order: 4;
                    padding: 20px 0;
                    font-size: 1rem;
                }
            }
        }
        & .detail_wrapper {
            width: 50%;
            // background-color: rgb(192, 192, 43);
            height: 470px;
            position: relative;
            @media screen and (max-width: 768px) {
                width: 100vw;
                margin-top: -40px;
                padding-top: 20px;
                z-index: 99;
                background-color: white;
                border-radius: 12px 12px 0 0;
                height: auto;
            }
            & .brand {
                position: relative;
                @media screen and (min-width: 1700px) {
                    position: unset;
                }
                width: 100%;
                text-align: center;
                > .limited,
                .back {
                    position: absolute;
                    width: max-content;
                    padding: 0 10px;
                    padding-left: 18px;
                    height: 30px;
                    background-color: $blue;
                    box-shadow: unset;
                    font-size: 1.3rem;
                    color: white;
                    font-weight: 600;
                    text-align: center;
                    right: 0;
                    top: 15px;
                    border-radius: 8px 0 0 8px;
                    @media screen and (min-width: 1700px) {
                        top: 100px;
                    }
                }
                > .back {
                    padding-left: 20px;
                    font-size: 2.3rem;
                    background-color: white;
                    color: $blue;
                    left: 0;
                    cursor: pointer;
                    right: unset;
                    top: 15px;
                    transition: 0.3s ease;
                    &:hover {
                        transform: translateX(-5px);
                    }
                }
                .logo,
                div {
                    margin: 0 auto;
                    width: 65px;
                    height: 65px;
                    border-radius: 800rem;
                    position: relative;
                    text-align: center;
                    box-shadow: 0 0 3px $fadeClr;
                    @include flex(center);
                    img {
                        width: 90%;
                        height: 90%;
                        object-fit: cover;
                    }
                }
            }
            & .info {
                height: 315px;

                & h2 {
                    width: 100%;
                    text-align: center;
                    padding: 25px 0;
                    font-size: 2.1rem;
                    color: $blue;
                    @media screen and (max-width: 768px) {
                        padding: 10px 0;
                        font-size: 1.8rem;
                    }
                }
                & .more_para {
                    text-align: center;
                    margin-top: 20px;
                    & p {
                        font-size: 1.4rem;
                        padding: 7px 15px;
                        background-color: $blue;
                        color: white;
                        cursor: pointer;
                        border-radius: 8px;
                        width: max-content;
                        margin: 0 auto;
                        transition: 0.25s ease;
                        &:hover {
                            opacity: 0.9;
                        }
                    }
                }
                & .para {
                    margin-top: 15px;
                    @include flex(center);
                    flex-wrap: wrap;
                    gap: 5px;
                    @media screen and (max-width: 768px) {
                        margin-left: 0px;
                        gap: 2px;
                    }
                    @include flex(center);
                    & > div {
                        width: 147px;
                        height: 100px;
                        padding: 5px 20px;
                        color: $fadeClr;
                        border: 2px solid $fadeClr500;
                        border-radius: 8px;
                        @media screen and (max-width: 768px) {
                            width: 41vw;
                            height: 60px;
                            padding: 10px;
                            border: 1px solid rgba(36, 36, 36, 0.06);
                            border-radius: 8px;
                        }
                        & span {
                            font-size: 1.35rem;
                            @media screen and (max-width: 768px) {
                                font-size: 1.15rem;
                                margin-left: 5px;
                            }
                        }
                        & div {
                            padding-bottom: 15px;
                        }
                        & h5 {
                            font-size: 1.5rem;
                            color: $textClr;
                            @media screen and (max-width: 768px) {
                                font-size: 1.25rem;
                                margin-left: 5px;
                            }
                        }
                    }
                }
            }
        }
    }
    & .margin {
        padding-top: 0px;
    }
    & .content {
        margin-top: 80px;
        padding-left: 25px;
        width: 100%;
        height: auto;
        display: flex;
        position: relative;
        @media screen and (max-width: 768px) {
            flex-direction: column;
            align-items: center;
            padding-left: unset;
            margin-top: unset;
            width: 95vw;
        }
        column-gap: 40px;
        & .main {
            width: 70vw;
            height: auto;
            @include flex(center);
            flex-direction: column;
            justify-content: flex-start;
            padding-bottom: 30px;
            @media screen and (max-width: 768px) {
                width: 95vw;
                padding-bottom: 10px;
            }
            h1,
            h2 {
                font-size: 2rem;
                margin: 0;
                padding: 0;
                color: $blue;
                text-align: left;
                padding-bottom: 20px;
                width: 100%;
                @media screen and (max-width: 768px) {
                    font-size: 2rem;
                }
            }
            p {
                font-size: 1.6rem;
                color: $textClr;
                text-align: justify;
                line-height: 1.6;
                @media screen and (max-width: 768px) {
                    font-size: 1.5rem;
                }
            }
            img {
                display: block;
                width: 100%;
                border-radius: 8px;
                margin-top: 10px;
                margin-bottom: 5px;
            }
            i {
                font-size: 1.6rem;
                display: block;
                color: $textClr;
                margin-bottom: 20px;
                @media screen and (max-width: 768px) {
                    font-size: 1.2rem;
                }
            }
            .review {
                width: 100%;
                text-align: center;
            }
            .comment_box {
                width: 100%;
                margin: 30px 0;
                z-index: 9 !important;
                position: relative;
                min-height: 400px;
                transition: 0.35s ease-in-out;
                @media screen and (max-width: 768px) {
                    min-height: 280px;
                }
                &::after {
                    position: absolute;
                    content: "";
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: transparent;
                    pointer-events: none;
                }
                h3 {
                    margin-bottom: 30px;
                    @media screen and (max-width: 768px) {
                        margin-bottom: 20px;
                    }
                }
            }

            .comment {
                margin-top: 20px;
                text-align: left;
                font-size: 1.8rem;
                color: white;
                background-color: $blue;
                padding: 5px 10px;
                border-radius: 5px;
                cursor: pointer;
                min-height: 280px;
                transition: 0.35s ease-in-out;
                &:hover {
                    opacity: 0.9;
                }
            }
        }
        & .side {
            .side_blog {
                width: 100%;
                height: 500px;
                position: sticky;
                top: 80px;
                right: 0;
                @media screen and (max-width: 768px) {
                    height: auto;
                }
            }
            width: 22vw;
            display: flex;
            flex-direction: column;
            gap: 20px;
            @media screen and (max-width: 768px) {
                width: 100vw;
                gap: 0px;
                h3 {
                    margin-left: 2.5vw;
                }
            }

            .brand_more {
                font-size: 1.2rem;
                width: max-content;
                margin: 0 auto;
                display: block;
                padding-bottom: 2px;
                text-align: center;
                margin-bottom: 2rem;
                cursor: pointer;
                color: $blue;
                font-weight: 600;
                border-bottom: 1px dotted transparent;
                transition: 0.25s ease;
                margin-top: 15px;

                &:hover {
                    border-bottom: 1px dotted $blue;
                }
            }
            .side_wrapper {
                width: 90%;
                @media screen and (max-width: 768px) {
                    width: 95vw;
                    padding: 0 15px;
                }
                .side_item {
                    @include flex(center);
                    cursor: pointer;
                    gap: 20px;
                    border-bottom: 1px dotted $fadeClr500;
                    padding: 25px 5px;
                    justify-content: space-between;
                    @media screen and (max-width: 768px) {
                        padding: 20px 17px;
                        order: 7 !important;
                    }
                    .side_same {
                        display: flex;
                        gap: 15px;
                    }
                    &:hover {
                        i {
                            transform: translateX(10px);
                            @media screen and (max-width: 768px) {
                                transform: translateX(0px);
                            }
                        }
                    }
                    i {
                        transition: 0.25s ease;
                        color: $blue;
                        transform: translateX(0px);
                        @media screen and (max-width: 768px) {
                            transform: translateX(-10px);
                        }
                    }

                    img {
                        width: 130px;
                        @media screen and (max-width: 768px) {
                            width: 120px;
                        }
                    }

                    > div {
                        @include flex(center);
                        flex-direction: column;
                        align-items: flex-start;
                        width: 150px;
                        > p:nth-child(1) {
                            font-size: 1.5rem;
                            font-weight: 600;
                            color: $blue;
                            padding-bottom: 7px;
                            @media screen and (max-width: 768px) {
                                font-size: 1.25rem;
                            }
                        }
                        > p:nth-child(2) {
                            font-size: 1.2rem;
                            font-weight: 500;
                            color: $textClr;
                            position: relative;
                            padding-left: 5px;
                            @media screen and (max-width: 768px) {
                                font-size: 1.1rem;
                            }
                            &::after {
                                content: "";
                                position: absolute;
                                width: 5px;
                                height: 5px;
                                border-radius: 50%;
                                background-color: $mainClr;
                                top: 50%;
                                left: -5px;
                                transform: translateY(-50%);
                            }
                        }
                        .side_same {
                            padding-left: 0px !important;
                            &::after {
                                content: unset !important;
                            }
                        }
                    }
                }
            }
        }
    }
}
.bread_crumb {
    width: 65vw;
    margin-top: 10px;
    @media screen and (max-width: 768px) {
        width: 100%;
        padding-top: 0px;
    }
}
.wrapper_swiper {
    width: 100%;
    padding-bottom: 10px;
    .swiper_item {
        height: 170px;
        font-size: 1.3rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        @media screen and (max-width: 768px) {
            font-size: 1.2rem;
            height: 140px;
        }
        .swiper_item-link {
            & img {
                width: 140px;
                height: 74px;
                transition: 0.25s ease;
                cursor: default;
                @media screen and (max-width: 768px) {
                    width: 100px;
                    height: 53px;
                }
                &:hover {
                    transform: scale(1.03);
                }
            }
            & div {
                @include flex(center);
                flex-direction: column;
                align-items: flex-start;
                width: 100px;
                gap: 2px;
                margin-left: 5px;
                color: $textClr;
                h4 {
                    color: $blue;
                    min-width: 125px;
                    cursor: pointer;
                }
                .car_info {
                    font-size: 1.2rem;
                    position: relative;
                    margin-left: 10px;
                    @media screen and (max-width: 768px) {
                        font-size: 1rem;
                    }
                    span {
                        margin-left: 10px;
                        width: max-content;
                        background-color: $fadeClr500;
                        padding: 1px 5px;
                        border-radius: 3px;
                        @media screen and (max-width: 768px) {
                            font-size: 0.9rem;
                        }
                    }
                    &::after {
                        content: "";
                        position: absolute;
                        top: 42%;
                        left: -10px;
                        transform: translateY(-50%);
                        width: 5px;
                        height: 5px;
                        background-color: $mainClr;
                        border-radius: 100rem;
                    }
                }
            }
        }
    }
}
