@import "../../GlobalStyles/GlobalStyles.scss";
.wrapper {
    z-index: 999999999;
    &.ok {
        pointer-events: all;

        .overlay {
            opacity: 0.2;
            z-index: 0;
            @media screen and (max-width: 768px) {
                opacity: 0.3;
            }
        }
    }
    .overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw !important;
        height: 100vh !important;
        background-color: black;
        opacity: 0;
        transition: 0.3s ease-in-out;
    }
    position: fixed;
    width: 100vw !important;
    height: 100vh !important;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    gap: 20px;
    pointer-events: none;

    > i {
        position: absolute;
        top: 45%;
        right: 7px;
        border-radius: 100rem;
        width: 40px;
        height: 40px;
        font-size: 2.5rem;
        background-color: white;
        font-weight: 800;
        color: $blue;
        @include flex(center);
        cursor: pointer;
        pointer-events: all !important;
        z-index: 9;
        box-shadow: 0 0 3px $textClr2;
        transition: 0.35s ease-in-out;
        opacity: 0.84;
        @media screen and (min-width: 769px) {
            font-size: 2.8rem;
            width: 45px;
            height: 45px;
            right: 15px;
            top: 40%;
            &:hover {
                background-color: $blue;
                color: white;
            }
        }

        &.active {
            background-color: $blue;
            color: white;
        }
    }

    .box {
        &.onresult {
            height: 75vh;
            width: 35vw;
            max-width: 600px;
            justify-content: space-between;
            gap: 0;
            padding-top: 40px;
            box-shadow: 0 0 5px $fadeClr;
            @media screen and (max-width: 768px) {
                top: 45%;
                height: 70vh;
                width: 97vw;
            }
            & .result {
                display: flex;
                height: 60vh;
                @media screen and (max-width: 768px) {
                    height: 56vh;
                }
            }
            & .submit {
                display: none;
            }
        }
        &.show {
            transform: translate(50%, -40%);
            opacity: 1;
            @media screen and (max-width: 768px) {
                transform: translate(-50%, -50%) !important;
            }
        }
        transform: translate(120%, -40%);
        opacity: 0;
        position: absolute;
        top: 40%;
        right: 50%;
        width: 480px;
        height: 250px;
        background-color: white;
        box-shadow: 0 0 5px $fadeClr;
        border-radius: 8px;
        @include flex(center);
        flex-direction: column;
        gap: 40px;
        transition: 0.2s ease-in-out;
        @media screen and (max-width: 768px) {
            transform: scale(0.0001);
            top: 40%;
            left: 50%;
            z-index: 99;
            width: 97vw;
            height: 230px;
            transition: 0.2s ease-in-out;
            transform-origin: 0 0;
        }
        .result {
            display: none;
            width: 100%;
            height: 380px;
            overflow-y: scroll;
            overflow-x: hidden;
            transition: 0.25s ease;
            &::-webkit-scrollbar {
                display: none;
            }

            h5 {
                text-align: center;
                color: $blue;
                padding: 15px 0;
                font-size: 1.5rem;
                min-height: 2rem;
                transform: translateX(50%);
                opacity: 0.9;
                & > i {
                    padding-right: 5px;
                }
                @media screen and (max-width: 768px) {
                    font-size: 1.25rem;
                }
            }
            .seethis {
                width: 100%;
                padding: 40px 0;
                & p {
                    background-color: $blue;
                    text-align: center;
                    width: max-content;
                    transform: translateX(-50%);
                    padding: 2px 10px;
                    color: white;
                    border-radius: 5px;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    max-width: 250px;
                    overflow: hidden;
                    font-size: 1.35rem;
                    @media screen and (max-width: 768px) {
                        font-size: 1.25rem;
                        max-width: 150px;
                    }
                    &:hover {
                        opacity: 0.9;
                    }
                }
            }
            & > div {
                width: 100%;
                & > div {
                    padding: 10px 5px;
                    padding-bottom: 12px;
                    border-bottom: 1px dotted $fadeClr500;
                    p {
                        font-size: 1.25rem;
                        color: $textClr;
                        @media screen and (max-width: 768px) {
                            font-size: 1rem;
                        }
                        &:nth-child(1) {
                            padding-left: 15px;
                            min-height: 20px;
                            @media screen and (max-width: 768px) {
                                padding-left: 10px;
                                opacity: 0.8;
                            }
                        }
                        &:nth-child(2) {
                            padding-top: 10px;
                            text-align: center;
                            color: $blue;
                            font-size: 1.8rem;
                            position: relative;
                            width: 100%;
                            @media screen and (max-width: 768px) {
                                font-size: 1.3rem;
                            }
                        }
                    }
                }
            }
        }
        > p {
            font-weight: 600;
            background-color: $blue;
            // background-color: $grayClr;
            // opacity: 0.7;
            color: white;
            padding: 2px 20px;
            border-radius: 5px;
            box-shadow: 0 0 5px $fadeClr300;
            cursor: pointer;
            z-index: -1;
            transition: 0.3s ease-in-out;
            @media screen and (max-width: 768px) {
                box-shadow: 0 0 0px $fadeClr300;
                font-size: 1.25rem;
            }
            &.disable {
                opacity: 0.6;
                pointer-events: none;
            }
        }

        .car_wrapper {
            @include flex(center);
            gap: 50px;
            position: relative;
            @media screen and (max-width: 768px) {
                gap: 20px;
            }
            strong {
                color: $blue !important;
                font-size: 3rem;
                @media screen and (max-width: 768px) {
                    font-size: 2rem;
                }
            }
            .car,
            .chose {
                text-align: center;
                min-width: 150px;
                min-height: 100px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                flex-direction: column;
                color: $blue;
                position: relative;
                @media screen and (max-width: 768px) {
                    min-width: 130px;
                    min-height: 90px;
                }
                .seeing {
                    position: absolute;
                    top: -27px;
                    left: 0px;
                    background-color: $fadeClr500;
                    padding: 0 5px;
                    padding-bottom: 1px;
                    font-size: 1.2rem;
                    @media screen and (max-width: 768px) {
                        font-size: 1rem;
                        top: -24px;
                        border-radius: 5px;
                    }
                }
                b {
                    position: absolute;
                    top: -18px;
                    right: 2px;
                    cursor: pointer;
                    opacity: 0.9;
                }
                span {
                    font-size: 1.2rem;
                }
                i {
                    margin-top: 15px;
                    font-size: 35px;
                    cursor: pointer;
                    transition: 0.25s ease-in-out;
                    &:hover {
                        transform: scale(1.05);
                    }
                }
                p {
                    font-size: 1.6rem;
                    font-weight: 600;
                    color: $blue;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    max-width: 150px;
                    overflow: hidden;
                    @media screen and (max-width: 768px) {
                        font-size: 1.25rem;
                        max-width: 120px;
                    }
                }
                img {
                    width: 110px;
                }
            }
        }
        .car_select {
            position: absolute;
            width: 650px;
            height: 350px;
            background-color: white;
            border-radius: 8px;
            overflow: hidden;
            display: flex;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            box-shadow: 0 0 5px $fadeClr;
            // transform: scale(1);
            margin-right: 155px;
            @media screen and (max-width: 768px) {
                width: 97vw;
                top: 80%;
                left: 50%;
                height: 70vh;
                flex-direction: column;
                transition: 0.2s ease-in-out;
                transform-origin: 0 0;
                transform: translate(-50%, -50%) !important;
            }
            &.hide {
                transform: scale(0.001) !important;
            }
            .car_wrap {
                span {
                    font-size: 1.35rem;
                    margin-left: -50px;
                    display: block;
                    width: 62%;
                    position: absolute;
                    top: 0px;
                    left: 48%;
                    font-weight: 600;
                    color: $blue;
                    background-color: white;
                    z-index: 9999;
                    padding-top: 5px;
                    @media screen and (max-width: 768px) {
                        width: 100vw;
                        margin-left: 0;
                        left: 0;
                        text-align: center;
                        padding-top: 10px;
                    }
                    & > i {
                        font-size: 2rem;
                        position: absolute;
                        top: 0px;
                        right: 10px;
                        padding: 0px 17px;
                        opacity: 0.8;
                        cursor: pointer;
                        @media screen and (max-width: 768px) {
                            padding: 2px 17px;
                        }
                        &:hover {
                            opacity: 1;
                        }
                    }
                }
                flex: 1;
                border-left: 2px solid $fadeClr300;
                padding: 35px 20px;
                flex-wrap: wrap;
                overflow-y: auto;
                gap: 40px;
                @include flex(center);
                column-gap: 55px;
                @media screen and (max-width: 768px) {
                    position: relative;
                    height: 40%;
                    border-bottom: 2px solid $fadeClr500;
                    overflow: hidden;
                }
                .car_item {
                    cursor: pointer;
                    text-align: center;
                    &:hover {
                        img {
                            transform: scale(1.03);
                        }
                    }
                    img {
                        transition: 0.35s ease-in-out;
                        width: 110px;
                    }
                    p {
                        font-size: 1.3rem;
                        font-weight: 600;
                        color: $blue;
                    }
                }
            }
        }
    }
}

.list_wrap {
    @media screen and (max-width: 768px) {
        ::-webkit-scrollbar {
            width: 4px !important;
            background: transparent; /* make scrollbar transparent */
        }
    }
    display: flex;
    position: sticky;
    right: 10px;
    top: 90px;
    @media screen and (max-width: 768px) {
        // position: fixed;
        position: unset;
        right: unset;
        top: unset;
        transform: unset;
        margin-right: 0px;
        order: 7 !important ;
        // height: auto;
        height: 45%;
        // margin-top: 40px;
        // transform: translateY(40px);
    }
    .list > ul {
        direction: rtl;
        width: 240px;
        height: 350px;
        overflow-y: scroll;
        overflow-x: hidden;
        display: flex;
        align-items: flex-end;
        flex-direction: column;
        justify-content: flex-start;
        list-style: none;
        scroll-behavior: smooth;
        @media screen and (max-width: 768px) {
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }

        & .list_item {
            padding: 0rem 0rem;
            width: 220px;
            height: 60px;
            padding-left: 3rem;
            @include flex(center);
            justify-content: flex-end;
            gap: 30px;
            border-bottom: 1px dotted $fadeClr500;
            transition: 0.2s ease;
            cursor: pointer;
            &:hover {
                background-color: $fadeClr500;
            }
            @media screen and (max-width: 768px) {
                width: 100vw;
                padding-left: 3rem;
                height: 60px;
                &:hover {
                    background-color: $fadeClr500;
                }
            }
            & > img {
                width: 42px;
                height: 42px;
            }
            & > span {
                font-size: 1.3rem;
                color: $textClr;
                font-weight: 500;
            }
        }
    }
}

.wrapper3 {
    width: 100vw;
    margin-top: -10px;
    .wrapper_swiper {
        overflow-y: hidden;
        .swiper {
            width: 100%;
            height: 240px;
            .swiper_item {
                @include flex(center);
                row-gap: 0px;
                height: 120px;
                h4 {
                    font-size: 1.3rem;
                    color: $blue;
                    text-align: center;
                }
                & img {
                    width: 115px;
                }
                &:hover {
                    img {
                        transform: scale(1.02);
                    }
                }
            }
        }
    }
}
