@import "./variable";
@import "./mixin";
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,300&display=swap");
html {
    font-size: 62.5%;
    @media screen and (max-width: 768px) {
        // scroll-behavior: smooth;
    }
}
body {
    font-family: "Roboto", sans-serif;
    font-size: 1.8rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
    -webkit-font-smoothing: antialiased;
    width: 100vw;
    overflow-x: hidden !important;
    z-index: 0;
}
@media screen and (max-width: 768px) {
}
* {
    padding: 0;
    margin: 0;
    text-decoration: none;
    color: unset;
    border: none;
    outline: none;
    background-color: unset;
    -webkit-tap-highlight-color: transparent;
    &::selection {
        background-color: $blue;
        color: white;
    }
    &:hover {
    }
}
button {
    cursor: pointer;
}

:global(.swiper-pagination-bullet) {
    background: $fadeClr;
    width: 15px;
    height: 3px;
    border-radius: 0.5rem;
    opacity: 0.8;
    transition: 0.25s ease;
    @media screen and (max-width: 768px) {
        height: 2px;
    }
}
img {
    user-select: none;
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    user-drag: none;
}
:global(.swiper-pagination-bullet-active) {
    background-color: $blue;
    width: 20px;
    height: 3px;
    border-radius: 0.5rem;
    opacity: 1;
    @media screen and (max-width: 768px) {
        height: 2px;
    }
}
:global(.swiper-pagination-bullet) {
    background: $fadeClr;
    width: 15px;
    height: 3px;
    border-radius: 0.5rem;
    opacity: 0.8;
    transition: 0.25s ease;
    @media screen and (max-width: 768px) {
        height: 2px;
    }
}

:global(.swiper-pagination-bullet-active) {
    background-color: $blue;
    width: 20px;
    height: 3px;
    border-radius: 0.5rem;
    opacity: 1;
    @media screen and (max-width: 768px) {
        height: 2px;
    }
}

:global(.swiper-scrollbar-drag) {
    background-color: $blue !important;
    height: 2px !important;
}
:global(.swiper-scrollbar-horizontal) {
    height: 2px !important;
    background-color: $fadeClr500 !important;
}
.ontop {
    @media screen and (max-width: 768px) {
        transform: translateY(-100%) !important;
    }
}
.find {
    cursor: pointer;
    color: $textClr;
    font-size: 2rem;
    margin-top: 1.5px;
    transition: 0.35s ease;
    text-align: center;
    font-weight: bold;
    display: none;
    @media screen and (max-width: 768px) {
        display: block;
        position: absolute;
        right: 55px;
        top: 10px;
    }
    &:hover {
        color: $blue;
    }
}
.logo {
    height: 45px;
    width: 170px;
    cursor: pointer;
    transition: 0.35s ease;
    & img {
        width: 100%;
        height: 100%;
    }
    @media screen and (max-width: 768px) {
        height: 37px;
        width: 130px;
        margin-left: 5px;
    }
    &:active {
        transform: scale(1.03);
    }
}
h3 {
    font-weight: 500;
    font-size: 1.8rem;
    color: $blue;
    border-radius: 8px;
    margin-left: 5px;
    position: relative;
    padding-left: 10px;
    margin-bottom: 5px;
    width: 100%;
    text-align: left;
    &::after {
        content: "";
        position: absolute;
        width: 4px;
        height: 55%;
        left: -1px;
        bottom: 50%;
        transform: translateY(50%);
        background-color: $blue;
    }
    @media screen and (max-width: 768px) {
        font-size: 1.4rem;
        margin-left: 0px;
        &::after {
            transform: translateY(45%);
        }
    }
}
