@import "../GlobalStyles/GlobalStyles.scss";
.history_wrap {
    width: 73vw;
    height: 180px;
    margin-top: 10px;
    @media screen and (max-width: 768px) {
        width: 100%;
        margin-top: -5px;
        height: 155px;
    }
    & > p {
        font-size: 1.2rem;
        font-weight: bold;
        color: $textClr;
        background-color: $fadeClr500;
        width: fit-content;
        padding: 2px 5px;
        border-radius: 5px;
        @media screen and (max-width: 768px) {
            margin-left: 15px;
            display: block;
        }
    }
    .wrapper_swiper {
        width: 100%;
        height: 120px;
        .swiper_item {
            height: 170px;
            font-size: 1.3rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            @media screen and (max-width: 768px) {
                font-size: 1.2rem;
            }
            .swiper_item-link {
                & img {
                    width: 140px;
                    height: 74px;
                    transition: 0.35s ease-in-out;
                    cursor: default;
                    @media screen and (max-width: 768px) {
                        width: 100px;
                        height: 53px;
                    }
                    &:hover {
                        transform: scale(1.03);
                    }
                }
                & div {
                    @include flex(center);
                    flex-direction: column;
                    align-items: flex-start;
                    width: 100px;
                    gap: 2px;
                    margin-left: 15px;
                    color: $textClr;
                    h4 {
                        color: $blue;
                        min-width: 125px;
                        cursor: pointer;
                    }
                    .car_info {
                        font-size: 1.2rem;
                        position: relative;
                        margin-left: 10px;
                        @media screen and (max-width: 768px) {
                            font-size: 1rem;
                        }
                        &::after {
                            content: "";
                            position: absolute;
                            top: 42%;
                            left: -10px;
                            transform: translateY(-50%);
                            width: 5px;
                            height: 5px;
                            background-color: $mainClr;
                            border-radius: 100rem;
                        }
                    }
                }
            }
        }
    }
}
