@import "../GlobalStyles/GlobalStyles.scss";
.wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 60px;
    box-shadow: 0 0 1px $darkClr;
    background-color: white;
    z-index: 99999;
    transition: 0.35s ease-in-out;
    @include flex(center);
    @media screen and (max-width: 768px) {
        height: 50px;
        box-shadow: 0 0 0px $darkClr;
        transform: translateY(0%);
    }

    .inner {
        height: 100%;
        width: 100vw;
        padding: 0 30px;
        @include flex(between);
        @media screen and (max-width: 768px) {
            padding: 0 5px;
            padding-right: 0px;
        }
    }

    .back {
        cursor: pointer;
        color: $textClr;
        font-size: 2.3rem;
        margin-top: 3px;
        transition: 0.35s ease;
        text-align: center;
        font-weight: bold;
        padding: 0 20px;
    }

    .bar {
        cursor: pointer;
        color: $textClr;
        font-size: 2.3rem;
        margin-top: 3px;
        transition: 0.35s ease;
        text-align: center;
        font-weight: bold;
        display: none;
        @media screen and (max-width: 768px) {
            display: block;
            padding-right: 3vw;
        }
        &:hover {
            color: $blue;
        }
        &:active {
        }
    }
}
