@import "../../GlobalStyles/GlobalStyles.scss";
.para_wrap {
    width: 100%;
    @media screen and (max-width: 768px) {
        margin-top: 15px;
    }
    .price_wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        @media screen and (max-width: 768px) {
            width: 80%;
            margin: 0 auto;
            gap: 2px;
            flex-direction: row;
        }
        & > div {
            width: 80%;
            padding: 10px;
            padding-bottom: 15px;
            text-align: center;
            @media screen and (max-width: 768px) {
                border-radius: 8px;
                background-color: $fadeClr500;
            }
            & span {
                font-size: 1.15rem;
                color: $textClr;
            }
            & p {
                text-align: center !important;
                width: 100%;
                font-size: 1.25rem !important;
                font-weight: 600;
                color: $blue !important;
            }
        }
    }
    .para_title_wrap {
        display: flex;
        .long {
            font-size: 1.8rem;
            color: $blue;
            margin-right: 2.3vw;
            cursor: pointer;
            margin-top: -4px;
            transition: 0.3s ease;
            padding: 0 3px;
            border-radius: 3px;
            height: 25px;
            @media screen and (max-width: 768px) {
                margin-right: 5px;
            }
            &.active {
                color: white;
                background-color: $blue;
            }
        }
        .para_title {
            margin-bottom: 20px;
            @media screen and (max-width: 768px) {
                margin-bottom: 5px;
            }
        }
    }
    .para_container {
        padding-top: 5px;
        padding-bottom: 30px;
        width: 100%;
        @include flex(center);
        align-items: flex-start;
        align-items: center;
        flex-direction: column;
        gap: 15px;
        @media screen and (max-width: 768px) {
            padding-top: 20px;
            gap: 10px;
        }
        .para_item {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 95%;
            cursor: pointer;
            transition: 0.35s ease-in-out;
            @media screen and (max-width: 768px) {
                width: 100%;
            }
            .para_item_head {
                pointer-events: none;
                border-radius: 5px;
                width: 100%;
                height: 45px;
                font-size: 1.6rem;
                @include flex(between);
                border: 1px solid $fadeClr300;
                color: $textClr;
                font-weight: 500;
                transition: 0.3s ease-in-out;
                z-index: 1;
                @media screen and (max-width: 768px) {
                    box-shadow: unset;
                    font-size: 1.3rem;
                }
                span {
                    padding: 0 20px;
                    user-select: none;
                    &:nth-child(2) {
                        transition: 0.25s linear;
                        color: $blue;
                    }
                }
            }
            &:hover {
                .para_item_head {
                    background-color: $fadeClr500;
                }
            }
            &.active {
                .para_item_head {
                    background-color: $blue;
                    border: 1px solid transparent;

                    color: white;
                    & > span:nth-child(2) {
                        transform: rotate(180deg);
                        color: white;
                    }
                }
                .para_item_hide {
                    max-height: 400px !important;
                }
            }
            .para_item_hide {
                width: 99%;
                max-height: 0;
                overflow: hidden;
                transition: 0.4s ease-in-out;
                @include flex(center);
                flex-direction: column;
                background-color: $fadeClr500;
                border-radius: 0px 0 5px 5px;
                box-shadow: 0 0 1px $fadeClr300;
                cursor: default;

                & > div {
                    width: 90%;
                    font-size: 1.5rem;
                    color: $textClr;
                    padding: 14px 20px;
                    border-bottom: 1px dotted $fadeClr300;
                    @media screen and (max-width: 768px) {
                        font-size: 1.2rem;
                        width: 82%;
                    }
                    &:last-child {
                        border-bottom: none;
                    }
                    & > span:nth-child(1) {
                        font-weight: 600;
                        min-width: 200px;
                        @media screen and (max-width: 768px) {
                            min-width: 120px;
                        }
                    }
                    display: flex;
                    justify-content: flex-start;
                    gap: 50px;
                    @media screen and (max-width: 768px) {
                        gap: 40px;
                    }
                }
            }
        }
    }
}
.price {
    display: block;
    color: $blue;
    text-align: center !important;
    width: 85vw;
    margin: 0 auto;
    margin-bottom: 40px;
    text-align: center;
    order: 4;
    font-size: 1rem;
    @media screen and (max-width: 768px) {
        margin-top: 15px;
    }
}
